export var serializeOptions = function (options) {
    var string = [];
    for (var key in options) {
        if (options.hasOwnProperty(key)) {
            string.push(encodeURIComponent(key) + "=" + encodeURIComponent(options[key]));
        }
    }
    return string.length > 0 ? '?' + string.join('&') : '';
};
